import React from 'react'
import moment from 'moment-timezone'
import './Clock.css'

moment.tz.setDefault('Australia/Lindeman')

class Clock extends React.Component {
  constructor(props) {
    super(props)
    this.state = { season: ['', ''], diff: 0, hasReachedZero: false }
  }

  componentDidMount() {
    this.timer = setInterval(() => this.tick(), 10)
  }

  componentWillUnmount() {
    clearInterval(this.timer)
  }

  tick() {
    var friday = moment()
      .day('Friday')
      .hours(0)
      .minutes(30)
      .seconds(0)
    var current = moment()
    var diff = friday - current
    var hrz = false
    if (diff <= 0) {
      diff = friday.week(friday.week() + 1) - current
      hrz = true
    }
    var d = convertTime(Math.round(diff / 1000))
    var s = this.state.hasReachedZero ? getSeason(1) : getSeason()
    this.setState({
      diff: d,
      season: s,
      hasReachedZero: hrz
    })
  }

  render() {
    return (
      <div className={'clock-container clock__' + this.state.season[0]}>
        <div className="clock">
          <h5>Current Season</h5>
          <h1>{this.state.season[0]}</h1>
          <h2>{this.state.season[1] + ' starts in:'}</h2>
          <div className="row">
            <div className="col">
              <span className="number">{this.state.diff[0]}</span>
              <span className="text">days</span>
            </div>
            <div className="col">
              <span className="number">{this.state.diff[1]}</span>
              <span className="text">hours</span>
            </div>
            <div className="col">
              <span className="number">{this.state.diff[2]}</span>
              <span className="text">minutes</span>
            </div>
            <div className="col">
              <span className="number">{this.state.diff[3]}</span>
              <span className="text">seconds</span>
            </div>
          </div>
          <p>Every Friday at 12:30 A.M. (AEDT), Forza Horizon 4's season changes.</p>
        </div>
      </div>
    )
  }
}

function convertTime(timeInSeconds) {
  var totalSeconds = timeInSeconds

  var days = Math.floor(totalSeconds / (3600 * 24))
  totalSeconds -= days * 3600 * 24
  var daysString = days > 0 ? (days > 9 ? days : '0' + days) : '00'

  var hours = Math.floor(totalSeconds / 3600)
  totalSeconds -= hours * 3600
  var hoursString = hours > 0 ? (hours > 9 ? hours : '0' + hours) : '00'

  var minutes = Math.floor(totalSeconds / 60)
  totalSeconds -= minutes * 60
  var minutesString =
    minutes > 0 ? (minutes > 9 ? minutes : '0' + minutes) : '00'

  var secondsString =
    totalSeconds > 0
      ? totalSeconds > 9
        ? totalSeconds
        : '0' + totalSeconds
      : '00'

  return [daysString, hoursString, minutesString, secondsString]
}

function getSeason(num = 0) {
  var week = (moment().week() % 4) + num
  var seasons = ['Winter', 'Spring', 'Summer', 'Autumn']
  switch (week) {
    case 0:
      return [seasons[2], seasons[3]]
    case 1:
      return [seasons[3], seasons[0]]
    case 2:
      return [seasons[0], seasons[1]]
    case 3:
      return [seasons[1], seasons[2]]
    default:
      throw new Error('getSeason error: Invalid number', week)
  }
}

export default Clock
